import React, { Component } from 'react';
import CardList from './CardList';
import {robots} from './robots';
import SearchBox from './SearchBox';
import './App.css';
import Scroll from './Scroll'

class App extends Component {
    constructor() {
        super()
        this.state = {
            robots: robots,
            SearchBar: ''
        }
    }

whileChanging = (event) =>   {
    this.setState({ SearchBar: event.target.value})
}


    render() {
        const filterprends = this.state.robots.filter(robots =>{
            return robots.name.toLowerCase().includes(this.state.SearchBar.toLocaleLowerCase())
        })
        return(
            
            <div className='tc'>
                <h1 className='hep f1'>Prends</h1>
                <SearchBox whileChange={this.whileChanging} />
                <Scroll>
                <CardList robots={filterprends} />
                </Scroll>
            </div>              
        );
    }
}


export default App;